.select {
  font-size: 11.96px;
  line-height: 23px;
  color: #737373;

  &__wrapper {
    position: relative;
    outline: none;
  }

  &__input {
    box-sizing: border-box;
    position: relative;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    min-height: 37px;
    background: #F8F8F7;
    border-radius: 10px;
    padding: 10px;
    cursor: pointer;

    &-drop-down-icon {
      background-image: url('../../../assets/icons/arrow-down.svg');
      height: 7px;
      width: 11px;
      transform: rotate(0deg);
      transition: all 0.25s ease-in-out;

      &_rotate {
        transform: rotate(180deg);
      }
    }
  }

  &__placeholder {
    white-space: pre;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #8C8C8C;
  }

  &__list,
  &__selected-list {
    margin: 0;
    list-style: none;
  }

  &__list {
    padding: 7px 0;
  }

  &__selected-list {
    position: absolute;
    padding: 0;
    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: max-content;
    column-gap: 10px;

    &-wrapper {
      position: relative;
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      overflow-y: hidden;
      overflow-x: auto;
    }
  }

  &__list {
    &-wrapper {
      border-top: 0;
      position: absolute;
      background: #F8F8F7;
      border-radius: 10px;
      width: 100%;
      max-height: 230px;
      overflow-y: scroll;
      z-index: 3;
      box-sizing: border-box;
      transition: all 0.25s ease-in-out;
      visibility: visible;
      margin-top: 3px;
      padding: 0 10px;
      box-shadow: 0px 20px 20px rgba(103, 103, 103, 0.3);
    }

    &_hidden {
      max-height: 0;
      visibility: hidden;
    }
  }

  &__selected-item {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #8C8C8C;
  }

  &__item {
    padding-left: 7px;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #8C8C8C;
    padding: 5px 0;
    cursor: pointer;
    &_selected {
      font-weight: bold;
      color: #565555;
    }

    &:hover {
      background-color: rgb(236, 236, 236);
    }
  }
}