.images-list {
  &__result {
    &-block {
      display: grid;
      grid-gap: 10px;
      grid-template-columns: repeat(3, 200px);
      grid-auto-rows: minmax(1px, 140px);
      margin-bottom: 10px;
    }
  }

  &__img {
    border-radius: 20px;
    object-fit: cover;
    overflow: hidden;
    height: 100%;
    width: 200px;

    &-1 {
      grid-column: 1/2;
      grid-row: 1/3;
    }

    &-2 {
      height: 140px;
    }

    &-3 {
      height: 140px;
    }

    &-4 {
      grid-column: 1;
      grid-row: 3;
      height: 140px;
    }

    &-5 {
      width: 410px;
      height: 290px;
      grid-row: 2/3;
    }
  }

  &__result-block_revert {
    .images-list__img {
      &-1 {
        width: 200px;
        grid-column: 1;
        grid-row: 1;
      }

      &-2 {
        width: 200px;
        height: 140px;
        grid-column: 2;
        grid-row: 1;
      }

      &-3 {
        width: 200px;
        height: 100%;
        grid-column: 3;
        grid-row: 1/3;
      }

      &-4 {
        grid-row: 2/3;
        width: 410px;
        height: 290px;
      }


      &-5 {
        width: 200px;
        height: 140px;
        grid-column: 3;
        grid-row: 3;
      }
    }
  }

  &__no-result {
    span {
      display: block;
      padding: 20px;
      background-color: #F8F8F7;
      border-radius: 20px;
    }
    height: 300px;
    margin-bottom: 20px;

  }
}