@import '../../../globalStyles/variables';

.upload-image {
  background: #F8F8F7;

  &__container {
    max-width: 680px;
    width: 100%;
    background-color: $mainBg;
    position: fixed;
    top: 0;
    right: 0;
    z-index: 2;
    margin: 30px;
    box-sizing: border-box;
    height: calc(100vh - 60px);
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 20px;
    padding: 100px 20px 40px;
    overflow: scroll;

    h2 {
      font-weight: 500;
      font-size: 36px;
      line-height: 52px;
      color: #1D1D1D;
    }

    a {
      color: $red;
    }
  }

  &__upload-box {
    background: #FFFFFF;
    border: 2px dashed #FBE0DC;
    border-radius: 20px;
    border: dashed 2px $lightRed;
    width: 100%;
    margin: 40px 0 0;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    max-height: 320px;
    cursor: pointer;
    padding: 20px 0;

    p {
      position: absolute;
      font-size: 20px;
      line-height: 30px;
      color: #8C8C8C;

      strong {
        color: #1D1D1D;
      }
    }

    &__default-photo {
      height: 200px;
    }
  }

  &__uploaded-photo {
    width: 100%;
    margin: 20px;
    object-fit: cover;
    height: 100%;
    border-radius: 10px;
  }

  &__inputphoto {
    position: absolute;
    visibility: hidden;
    opacity: 0;
  }

  &__bg {
    position: fixed;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(29, 29, 29, 0.6);
    z-index: 1;
  }

  &__info-mes {
    font-weight: 400;
    line-height: 30px;
    color: #8C8C8C;
    margin: 20px 0;
    font-size: 20px;
  }

  &__info-status {
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: #8C8C8C;
    margin: 20px 0;
    background: #FFFFFF;
    border-radius: 10px;
    padding: 20px;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    img {
      margin-right: 10px;
    }
  }

  &__form-upload_ondrag {
    border: dashed 2px $red;
  }

  &__close {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
  }
}