.user-logs {
  margin-top: 15px;
  &__log {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #F8F8F7;
    border-radius: 10px;
    padding: 15px 20px 15px 15px;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }
    div {
      display: flex;
      align-items: center;
    }

    span {
      background-color: #fff;
      padding: 3px 10px;
      border-radius: 5px;
      margin-right: 20px;
    }

    p {
      font-size: 16px;
      line-height: 24px;
      color: #8C8C8C;
    }

    strong {
      color: #1D1D1D;
      font-weight: 500;
    }
  }
}