@import '../../../globalStyles/_variables.scss';

.selected-c {
  position: relative;
  &__img {
    position: absolute;
    right: 0;
    width: calc(100% + 60px);
    height: calc(100vh + 60px);
    object-fit: contain;
    transform: translate(0px, -40px);
    z-index: 2;
    @media(min-width: 1300px) {
      right: 30px;
    }
  }

  &__container {
    background-color: #FBE0DC;
    height: calc(100vh - 60px);
    border-radius: 20px;
    margin: 30px 30px 30px 0;
  }
}