.gallery-filters {
  padding: 15px 20px 28px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  background: #F8F8F7;
  border-radius: 20px;
  margin-bottom: 20px;

  &__select-wrapp {
    margin-right: 20px;

    &:nth-child(2) {
      margin-right: 0;
    }

    &:nth-child(4) {
      margin-right: 0;
      label {
        margin-top: 5px;
      }
    }

    &:nth-child(3) {
      label {
        margin-top: 5px;
      }
    }

    label {
      display: block;
      font-weight: 500;
      font-size: 10px;
      line-height: 18px;
      color: #8C8C8C;
      margin-left: 10px;
    }

    .select__input {
      background-color: #fff;
    }
  }

  &__reset {
    display: flex;
    align-items: center;
    img {
      cursor: pointer;
    }
    .select__input {
      width: 240px;
      margin-right: 10px;
    }
  }
}