.search-container {
  padding-top: 20px;

  &__result {}

  &__result-for {
    font-size: 20px;
    line-height: 29px;
    color: #8C8C8C;

    strong {
      color: #1D1D1D;
    }
  }

  &__block {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__loader-container {
    width: 100%;
    height: 300px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}