.cat-card {
  width: fit-content;

  &:hover .cat-card__container {
    border: 4px solid #f8f8f7;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 130px;
    height: 190px;
    border-radius: 20px;
    border: 4px solid rgba(255, 255, 255, 0.6);
    margin-bottom: 10px;
  }

  &__violet-color {
    background: #B4B7FF;
  }

  &__green-color {
    background: #97EAB9;
  }

  &__orange-color {
    background: #FFD280;
  }

  & .button {
    width: 100%;
  }
}

.cat-card_active {
  .button {
    background: #FF868E;
    color: #fff;
  }

  .cat-card__container {
    border: 4px solid #FBE0DC;
  }
}

.cat-card_active {
  &:hover .cat-card__container{
    border: 4px solid #FBE0DC;
  }
}