.vote-img {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__loader {
    height: 360px;
    display: flex;
    align-items: center;

    .loader {
      margin-top: -40px;
    }
  }

  &__voting-block {
    display: flex;
    margin-top: -40px;
    border-radius: 30px;
    border: 5px solid #fff;
    background: #fff;
  }

  &__voting-button {
    width: 80px;
    height: 80px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;

    &.green-bg {
      &:hover {
        background-color: rgba(151, 234, 185, 0.3);
      }

      &:hover svg path {
        fill: #97EAB9;
      }
    }

    &.red-bg {
      &:hover {
        background-color: rgba(255, 134, 142, 0.3);
      }

      &:hover img {
        filter: invert(44%) sepia(51%) saturate(2878%) hue-rotate(326deg) brightness(135%) contrast(82%);
      }
    }

    &.orange-bg {
      &:hover {
        background-color: rgba(255, 210, 128, 0.3);
      }

      &:hover svg path {
        fill: #FFD280;
      }
    }

    &:first-child {
      border-top-left-radius: 31%;
      border-bottom-left-radius: 31%;
    }

    &:last-child {
      border-top-right-radius: 31%;
      border-bottom-right-radius: 31%;

    }
  }

}