.gallery {
  .image .button {
    width: 40px;
    height: 40px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    img {
      height: 20px;
    }
  }

  .aside-container {
    position: relative;
  }

  &__upload {
    position: absolute;
    top: 20px;
    right: 20px;
    padding: 0 30px;

    img {
      margin-right: 10px;
    }

    &:hover img {
      filter: contrast(15);
    }
  }
}