@import '../globalStyles/_variables.scss';

.home {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 1440px;
  margin: auto;

  main {
    position: relative;
    padding: 38px 50px 0;

    @media (min-width: $mediumDesktopWidth) {
      padding: 38px 133px 0;
    }
  }

  &__layout {
    position: sticky;
    top: 38px;
  }

  &__title {
    font-weight: 500;
    font-size: 44px;
    line-height: 58px;
    margin-top: 80px;
  }

  &__description {
    font-size: 20px;
    line-height: 29px;
    color: #8C8C8C;
    margin: 20px 0 60px;
  }

  &__text-action {
    display: block;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;
    margin-bottom: 25px;
  }

  aside {
    width: 100%;
    margin-left: auto;
  }
}