.aside-container {
  background: #FFFFFF;
  border-radius: 20px;
  padding: 20px;
  margin: 10px 30px 18px 0;

  &__back-block {
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    .button {
      cursor: initial;
      height: 40px;
      padding: 10px 30px;
      font-weight: 500;
      font-size: 20px;
      line-height: 30px;
    }
  }

  &__back {
    margin-right: 10px;
    cursor: pointer;
    height: 40px;
    width: 40px;
    background-image: url('../../../assets/icons/back.svg');

    &:hover {
      background-image: url('../../../assets/icons/back-hover.svg');
    }
  }
}