@import '../../../globalStyles/_variables.scss';

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 17px 8px 17px;
  border: none;
  cursor: pointer;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  outline: none;
  text-transform: uppercase;
  background: #FFFFFF;
  border-radius: 10px;
  height: 36px;
  font-family: 'Jost';
  color: #FF868E;

  &:hover {
    background: #FBE0DC;
  }
}

.button_active {
  background: #FF868E;
  color: #FFFFFF;

  &:hover {
    background: #FF868E;
    color: #fff;
  }
}

.button_secondary {
  background-color: #FBE0DC;

  &:hover {
    background-color: #FF868E;
    color: #fff;
  }
}

.button {
  &:disabled {
    cursor: initial;
    color: #8C8C8C;
    background-color: #F8F8F7;
  }
}