.br-filters {
  display: flex;
  justify-content: space-between;

  .select {
    margin-right: 10px;
  }

  &__breed {
    margin-left: 10px;
    width: 226px;
  }

  &__limit {
    width: 101px;
  }

  &__sort-img {
    cursor: pointer;
    margin-right: 10px;

    &:last-child {
      margin-right: 0;
    }
  }
}