.image {
  position: relative;

  &__img {
    width: 100%;
    object-fit: cover;
    height: 100%;
  }

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    opacity: 1;
  }

  &:hover:after {
    background-color: rgba(255, 134, 142, 0.6);
    opacity: 1;
  }

  .button {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 10px;
    opacity: 0;
    margin: 0 10px;
    font-weight: 400;

    &:hover {
      background-color: #fff;
    }
  }

  &:hover .button {
    opacity: 1;
    z-index: 2;
  }
}

.image__vote.button {
  width: 40px;
  height: 40px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  img {
    height: 20px;
  }
}