@import '../../../globalStyles/_variables.scss';

.search {
  display: flex;
  position: relative;
  max-width: 470px;
  height: 60px;
  width: 100%;

  input {
    width: 100%;
    background: #FFFFFF;
    border-radius: 20px;
    border: 2px solid #ffffff;
    padding: 15px 20px;
    outline: none;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    font-family: 'Jost';

    &::placeholder {
      font-weight: 400;
      color: #8C8C8C;
      font-family: 'Jost';
    }

    &:hover {
      border: 2px solid $lightRed;
    }

    &:focus {
      border: 2px solid $red;
    }
  }

  .button {
    width: 40px;
    height: 40px;
    padding: 0;
    position: absolute;
    right: 10px;
    top: 10px;
    border-radius: 10px;
    background-color: $lightRed;

    &:active {
      background-color: $red;
    }

    &:active img {
      filter: contrast(11.5) saturate(1.5);
    }

  }
}