.breed-details {
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(100%, 0);
  visibility: hidden;
  transition: 0.4s;

  &_active {
    width: 100%;
    transform: translate(0, 0);
    visibility: initial;
  }

  &__img {
    width: 100%;
    max-width: 680px;
    max-height: 360px;
    border-radius: 20px;
    object-fit: cover;
  }

  &__info {
    margin-top: 50px;
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    border: 2px solid #FBE0DC;
    border-radius: 20px;
    padding: 75px 40px 40px;

    &-block {
      margin-bottom: 10px;
    }

    strong {
      font-weight: 500;
      font-size: 16px;
      line-height: 23px;
      color: #1D1D1D;
    }

    span {
      color: #8C8C8C;
      max-width: 217px;
      margin-top: 10px;
      line-height: 23px;
      font-size: 16px;
    }
  }

  &__name {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    background-color: #fff;
    padding: 0 40px;
    top: -30px;
    font-weight: 500;
    font-size: 36px;
    line-height: 52px;
    width: fit-content;
    text-align: center;

    p {
      color: #8C8C8C;
      font-weight: 500;
      font-size: 20px;
      line-height: 29px;
    }
  }
}